import './App.css';
import './components/Boxlist.css'
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import DetailCity from './components/DetailCity';
import DetailYaris from './components/DetailYaris';
import DetailMazada2 from './components/DetailMazda2';
import DetailAlmera from './components/DetailAlmera';
import DetailAttrage from './components/DetailAttrage';
import DetailSwift from './components/DetailSwift';

function Homepage() {
  return (   
    <div>   
      <Container>
        <Row>         
            <Col>
              <DetailYaris/>
            </Col>
            <Col>
              <DetailMazada2/>
            </Col>
            <Col>
              <DetailCity/>
            </Col>
            <Col>
              <DetailAlmera/>
            </Col>
            <Col>
              <DetailAttrage/>
            </Col>
            <Col>
              <DetailSwift/>
            </Col>
          </Row>
        </Container>

    </div>
  );

}

export default Homepage;
