import Boxlistcity from './Boxlistcity';
import { v4 as uuidv4 } from 'uuid';

const DetailCity=()=>{

  const data = [{Brand:"Honda",Model:"City",Detail:" 1.0  122   173 -  1.8   23.8 ./",Price:"629,000"},
            
               ]

    return (
      <div >
          {data.map((elemant)=>{
          return <Boxlistcity {...elemant} key={uuidv4()}/>    
          })}
        
      </div>
    );
  }

  export default DetailCity;