import { v4 as uuidv4 } from 'uuid';
import BoxlistAttrage from './BoxlistAttrage';

const DetailAttrage=()=>{

  const data = [{Brand:"Mitsubishi",Model:"Attrage",Detail:"    *  Piano Black  Carbon Print",Price:"494,000"},
            
               ]

    return (
      <div >
          {data.map((elemant)=>{
          return <BoxlistAttrage {...elemant} key={uuidv4()}/>    
          })}
        
      </div>
    );
  }

  export default DetailAttrage;