import Carousel from 'react-bootstrap/Carousel';
import slide1 from './image/Swift/Swift.png';
import slide2 from './image/Swift/1.png';
import slide3 from './image/Swift/2.jpeg';
import slide4 from './image/Swift/3.jpg';
import slide5 from './image/Swift/4.jpg';
import slide6 from './image/Swift/5.jpg';
import './Nav.css';

function Swiftpic() {

  return (     
                <Carousel className='ali'>
                  <Carousel.Item>
                    <img
                      className="d-block w-100"
                      src={slide1}
                      alt="First slide"
                    />                  
                    <img
                      className="d-block w-100"
                      src={slide2}
                      alt="First slide"
                    />
                    <Carousel.Caption>
                      <h2>SUZUKI SWIFT</h2>
                      <p>POWER YOU UP     SUZUKI SWIFT  </p>
                    </Carousel.Caption>
                  </Carousel.Item>
                  <Carousel.Item>
                    <img
                      className="d-block w-100"
                      src={slide3}
                      alt="Second slide"
                    />
                    
                    <img
                      className="d-block w-100"
                      src={slide4}
                      alt="Second slide"
                    />

                    <Carousel.Caption>
                      <h2> </h2>
                      <p>  LCD  </p>
                    </Carousel.Caption>
                  </Carousel.Item>
                  <Carousel.Item>
                    <img
                      className="d-block w-100"
                      src={slide5}
                      alt="Third slide"
                    />
                    <img
                      className="d-block w-100"
                      src={slide6}
                      alt="Third slide"
                    />

                    <Carousel.Caption>
                      <h2> LED   Daytime Running Light  LED</h2>
                      <p>
                              
                      </p>
                    </Carousel.Caption>
                  </Carousel.Item>
                </Carousel>

  );
}

export default Swiftpic;