import BoxlistAlmera from './BoxlistAlmera';
import { v4 as uuidv4 } from 'uuid';

const DetailAlmera=()=>{

  const data = [{Brand:"Nissan",Model:"Almera",Detail:"   1.0L TURBO    Car of the Year 2023*  2022",Price:"589,000"},
            
               ]

    return (
      <div >
          {data.map((elemant)=>{
          return <BoxlistAlmera {...elemant} key={uuidv4()}/>    
          })}
        
      </div>
    );
  }

  export default DetailAlmera;