import Carousel from 'react-bootstrap/Carousel';
import slide1 from './image/Attrage/Attrage.jpg';
import slide2 from './image/Attrage/1.jpg';
import slide3 from './image/Attrage/2.jpg';
import slide4 from './image/Attrage/3.jpg';
import slide5 from './image/Attrage/4.jpg';
import slide6 from './image/Attrage/5.jpg';
import './Nav.css';

function Attragepic() {

  return (     
                <Carousel className='ali'>
                  <Carousel.Item>
                    <img
                      className="d-block w-100"
                      src={slide1}
                      alt="First slide"
                    />
                    <img
                      className="d-block w-100"
                      src={slide2}
                      alt="First slide"
                    />
                    <Carousel.Caption>
                      <h1> </h1>
                      <p> </p>
                    </Carousel.Caption>
                  </Carousel.Item>
                  <Carousel.Item>
                    <img
                      className="d-block w-100"
                      src={slide3}
                      alt="Second slide"
                    />
                    <img
                      className="d-block w-100"
                      src={slide4}
                      alt="Second slide"
                    />

                    <Carousel.Caption>
                      <h3> Projector  Bi-LED</h3>
                      <p> LED</p>
                    </Carousel.Caption>
                  </Carousel.Item>
                  <Carousel.Item>
                    <img
                      className="d-block w-100"
                      src={slide5}
                      alt="Third slide"
                    />
                    <img
                      className="d-block w-100"
                      src={slide6}
                      alt="Third slide"
                    />

                    <Carousel.Caption>
                      <h3>  3  LED</h3>
                      <p>
                         
                      </p>
                    </Carousel.Caption>
                  </Carousel.Item>
                </Carousel>

  );
}

export default Attragepic;