
import { v4 as uuidv4 } from 'uuid';
import BoxlistSwift from './BoxlistSwift';

const DetailSwift=()=>{

  const data = [{Brand:"Suzuki",Model:"Swift",Detail:"POWER YOU UP     SUZUKI SWIFT  ",Price:"567,000"}
            
               ]

    return (
      <div >
          {data.map((elemant)=>{
          return <BoxlistSwift {...elemant} key={uuidv4()}/>    
          })}
        
      </div>
    );
  }

  export default DetailSwift;