import './App.css';
import './components/Boxlist.css'

import Yarispic from './components/Yarispic';
import Homepage from './Home';
import Contact from './components/Contact';
import { Switch, Route } from 'react-router-dom/cjs/react-router-dom';
import Page404 from './components/Page404';
import Mazdapic from './components/Mazdapic';
import Citypic from './components/Citypic';
import Almerapic from './components/Almerapic';
import Attragepic from './components/Attragepic';
import Swiftpic from './components/Swiftpic';
import Navtop from './components/Navtop';

function App() {
  return (   
    
    <div>
      <Navtop/>
      <Switch>          
          <Route path='/' exact>
            <Homepage/>
          </Route>
          <Route path='/Yarispic'>
            <Yarispic/>
          </Route>
          <Route path='/Mazdapic'>
            <Mazdapic/>
          </Route>
          <Route path='/Citypic'>
            <Citypic/>
          </Route>
          <Route path='/Almerapic'>
            <Almerapic/>
          </Route>
          <Route path='/Attragepic'>
            <Attragepic/>
          </Route>
          <Route path='/Swiftpic'>
            <Swiftpic/>
          </Route>
          <Route path='/Contact'>
            <Contact/>
          </Route>
          <Route path='*'>
            <Page404/>
          </Route>
      </Switch>      
    </div>
  );

}

export default App;
