import React from "react";
import './Contact.css';
import Pindrop from './image/Pindrop.png';
import Call from './image/Call.png';
import Email from './image/Email.png';

function Contact() {
    return(

        <div className="BGC">
            <div className="block">           
            <figure>
                <img src={Pindrop} alt="Pindrop" style={{height: '50px', marginTop: '12px', opacity: '50%'}}/>
            </figure>
            <div>
                <h3>Location</h3>
                <p>Ladprao, Bankok 10230.</p>
            </div> 
            </div>
            <div className="block">
                <figure>
                    <img src={Call} alt="Call" style={{height: '50px', marginTop: '12px', opacity: '50%'}}/>
                </figure>
                <div>
                    <h3>Phone Number</h3>
                    <p>(66+) 86-461-1595</p>
                </div>
            </div>
            <div className="block">
                <figure>
                    <img src={Email} alt="Email" style={{height: '50px', marginTop: '12px', opacity: '50%'}}/>
                </figure>
                <div>
                    <h3>Email</h3>
                    <p>santisuk.ck@gmail.com</p>
                </div>
            </div>
        </div>

    )    

};

export default Contact;