import BoxlistYaris from './BoxlistYaris';
import { v4 as uuidv4 } from 'uuid';


const DetailYaris=()=>{

  const data = [{Brand:"Toyota",Model:"Yaris",Detail:".. Fastback Design   ",Price:"549,000"},
               
              ]
              return (
                 
                          <div >
                              {data.map((elemant)=>{

                              return  <BoxlistYaris {...elemant} key={uuidv4()}/>

                              })}
                          
                          </div>
                    

    );
  }

  export default DetailYaris;