import BoxlistMazda2 from './BoxlistMazda2';
import { v4 as uuidv4 } from 'uuid';

const DetailMazda2=()=>{

  const data = [{Brand:"Mazada",Model:"Mazada 2",Detail:" ",Price:"599,000"},
            
               ]

    return (
      <div >
          {data.map((elemant)=>{
          return <BoxlistMazda2 {...elemant} key={uuidv4()}/>    
          })}
        
      </div>
    );
  }

  export default DetailMazda2;