import Carousel from 'react-bootstrap/Carousel';
import slide1 from './image/Mazda2/Mazada2.jpg';
import slide2 from './image/Mazda2/1.jpg';
import slide3 from './image/Mazda2/2.jpg';
import slide4 from './image/Mazda2/3.jpg';
import slide5 from './image/Mazda2/4.jpg';
import slide6 from './image/Mazda2/5.jpg';
import slide7 from './image/Mazda2/6.jpg';
import slide8 from './image/Mazda2/7.jpg';
import './Nav.css';

function Mazadapic() {

  return (     
                <Carousel className='ali'>
                  <Carousel.Item>
                    <img
                      className="d-block w-100"
                      src={slide1}
                      alt="First slide"
                    />
                    <img
                      className="d-block w-100"
                      src={slide2}
                      alt="First slide"
                    />
                    <Carousel.Caption>
                      <h3>NEW MAZDA2 SPORT DESIGN</h3>
                      <p>NEW MAZDA2 NEW WAVE DESIGN</p>
                    </Carousel.Caption>
                  </Carousel.Item>
                  <Carousel.Item>
                    <img
                      className="d-block w-100"
                      src={slide3}
                      alt="Second slide"
                    />
                    <img
                      className="d-block w-100"
                      src={slide4}
                      alt="Second slide"
                    />

                    <Carousel.Caption>
                      <h3></h3>
                      <p> New Wave Design </p>
                    </Carousel.Caption>
                  </Carousel.Item>
                  <Carousel.Item>
                    <img
                      className="d-block w-100"
                      src={slide5}
                      alt="Third slide"
                    />
                    <img
                      className="d-block w-100"
                      src={slide6}
                      alt="Third slide"
                    />

                    <Carousel.Caption>
                      <h3>MAZDA CONNECT</h3>
                      <p>
                        THE DRIVING PLEASURE
                      </p>
                    </Carousel.Caption>
                  </Carousel.Item>
                  <Carousel.Item>
                    <img
                      className="d-block w-100"
                      src={slide7}
                      alt="Forth slide"
                      
                    />
                    <img
                      className="d-block w-100"
                      src={slide8}
                      alt="Forth slide"
                      
                    />

                    <Carousel.Caption>
                      <h3> 16 </h3>
                      <p>
                           15  
                      </p>
                    </Carousel.Caption>
                  </Carousel.Item>
                </Carousel>

  );
}

export default Mazadapic;