import Carousel from 'react-bootstrap/Carousel';
import slide1 from './image/City/City.jpg';
import slide2 from './image/City/1.jpg';
import slide3 from './image/City/2.jpg';
import slide4 from './image/City/3.jpg';
import slide5 from './image/City/4.jpg';
import slide6 from './image/City/5.jpg';
import slide7 from './image/City/6.jpg';
import slide8 from './image/City/7.jpg';
import './Nav.css';

function Citypic() {

  return (     
                <Carousel className='ali'>
                  <Carousel.Item>
                    <img
                      className="d-block w-100"
                      src={slide1}
                      alt="First slide"
                    />
                    <img
                      className="d-block w-100"
                      src={slide2}
                      alt="First slide"
                    />
                    <Carousel.Caption>
                      <h3>  RS  LED</h3>
                      <p> RS  LED</p>
                    </Carousel.Caption>
                  </Carousel.Item>
                  <Carousel.Item>
                    <img
                      className="d-block w-100"
                      src={slide3}
                      alt="Second slide"
                    />
                    <img
                      className="d-block w-100"
                      src={slide4}
                      alt="Second slide"
                    />

                    <Carousel.Caption>
                      <h3></h3>
                      <p></p>
                    </Carousel.Caption>
                  </Carousel.Item>
                  
                  <Carousel.Item>
                    <img
                      className="d-block w-100"
                      src={slide5}
                      alt="Forth slide"
                      
                    />
                    <img
                      className="d-block w-100"
                      src={slide6}
                      alt="Forth slide"
                      
                    />

                    <Carousel.Caption>
                      <h3></h3>
                      <p>
                         e:HEV Powerful Hybrid
                      </p>
                    </Carousel.Caption>
                  </Carousel.Item>
                  <Carousel.Item>
                    <img
                      className="d-block w-100"
                      src={slide7}
                      alt="Five slide"
                      
                    />
                    <img
                      className="d-block w-100"
                      src={slide8}
                      alt="Five slide"
                      
                    />

                    <Carousel.Caption>
                      <h3> (Honda LaneWatch) </h3>
                      <p>
                         3  (Multi-Angle Rearview Camera)
                      </p>
                    </Carousel.Caption>
                  </Carousel.Item>
                </Carousel>

  );
}

export default Citypic;