import Button from 'react-bootstrap/Button';
import Card from 'react-bootstrap/Card';
import PropTypes from 'prop-types';
import './Boxlist.css';
import Mycar from './image/Yaris/Yaris.png';

const BoxlistYaris=(props)=> {
  
    const {Brand,Model,Detail,Price} = props

  return (   

    <div className='B'>
             <div className='Card'>
              <Card style={{width: '18rem'}}>      
                <Card.Img variant="top" style={{height: '180px'}} src={Mycar} />
                <Card.Body >
                    <Card.Title>{Brand}</Card.Title>
                    <Card.Text>{Model}</Card.Text>
                    <Card.Text> {Price} </Card.Text>
                    <Card.Text className='thidden'>{Detail}</Card.Text>                   
                </Card.Body>
                <div className='Btn'>                 
                  <Button variant="outline-dark" style={{width: '200px'}} href='Yarispic'>View Photos</Button>                   
                </div>                
            </Card>  

             </div>
                        
    </div>  

  );
}
    BoxlistYaris.propTypes={
    Brand:PropTypes.string.isRequired,
    Price:PropTypes.string.isRequired
}


export default BoxlistYaris;

